}

a {
    padding: 3px;
}

.intro-text {
    color: aliceblue;
}
.links {
    text-align: center;
   
}

    
    

.link {
    size: 3rem;
    padding: 2px;
}

#footer {
    text-align: center;
    padding: 20px;
    color: grey;
  }
  
